/* Mobile View */
@media only screen and (max-width: 600px) {
  img.my-img {
    width: 300px;
    }
    *{
      padding: 0;
      margin: 0;
    }
    .card-content{
      display: inherit;
    }
    .container-row {
      padding: 0px 50px 120px 50px!important;
    }
    .btn-lm {
      font-size: 20px;
      width: 60%;
    }
    .profile {
      margin-left: 0px;
      width: 300px;
      padding: 20px 0px;
    }
    .card{
      width: 20rem!important;
    }
    h1{
    font-size: 60px!important;
  }
}
@media only screen and (max-width: 1024px){
  .card-content {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
  }
  
}


